"use client";

import { useState, useEffect } from "react";

export default function ActionsPage() {
  const [user, setUser] = useState(null);
  const [action, setAction] = useState("");
  const [actions, setActions] = useState([]);
  const [message, setMessage] = useState("");

  const getUser = async () => {
    const res = await fetch("/api/auth/me");
    const data = await res.json();
    if (data.user) setUser(data.user);
  };

  useEffect(() => { getUser(); }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user) return;

    const res = await fetch("/api/actions", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ description: action }),
    });
    const data = await res.json();
    if (res.ok) {
      setActions([...actions, data]);
      setAction("");
      setMessage("Action logged!");
    } else {
      setMessage(data.error);
    }
  };

  if (!user) return <p>Please log in to log actions.</p>;

  return (
    <div className="actions-page">
      <h2>Log an Action</h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          placeholder="Describe your eco-friendly action"
          value={action}
          onChange={(e) => setAction(e.target.value)}
          required
        />
        <button type="submit">Log Action</button>
      </form>
      {message && <p>{message}</p>}

      <h3>Recent Actions</h3>
      <ul>
        {actions.map((a, idx) => (
          <li key={idx}>{a.description}</li>
        ))}
      </ul>
    </div>
  );
}
